@media (max-width: 1280px) {
    header {
        .header__nav-container {
            .header__nav-wrapper {
                padding: 10px 30px 10px 30px;
                background-color: rgba($color: #000000, $alpha: .4);
            }
        }

    }

    section.portfolio {
        padding: 100px 0 40px;
    }

    section.contacts {
        padding: 115px 0 120px;
    }
}

@media (max-width: 1080px) {
    header {
        .header__firstscreen-container {
            max-width: 960px;
            margin-top: 130px;
            padding-bottom: 120px;

            .header__greeting {
                width: 100%;
                margin-right: 20px;

                h1.header__title {
                    min-height: 190px;
                    font-size: 48px;
                    line-height: 60px;
                }

                h1 img {
                    height: 35px;
                    margin-right: 3px;
                    animation: rotating .3s;
                }

                h1 span {
                    margin-right: 6px;
                }

                .line {
                    margin: 15px 0 40px;
                }

                .btn-container {
                    .btn-classic {
                        padding: 12px 30px;
                        font-size: 18px;
                    }

                    .maska {
                        font-size: 18px;
                        padding: 12px 30px;
                    }
                }
            }

            .header__animation {
                max-width: 400px;
                &.animated.active.slide-top{
                    animation-name: slideDown2;
                }
                img {
                    object-fit: contain;
                    max-width: 400px;
                }
            }
            .header__animation.animated.active.slide-down{
                animation: slideDown2 1s ease-in-out;
            }
        }
    }

    section.skills {
        padding: 110px 0;

        .section__description {
            margin-top: 15px;
        }

        .container {
            max-width: 960px;

            .skills__container {
                max-width: 415px;

                .skills__box {
                    margin-top: 40px;
                }
            }
        }
    }

    section.contacts {
        .back__text {
            right: -50%;
        }

        .form__container {
            align-items: start;
        }

        .maps {
            max-width: 400px;
        }
    }
}

@media (max-width: 968px) {
    header {
        .header__firstscreen-container {
            flex-direction: column;
            max-width: 960px;
            margin-top: 130px;
            padding-bottom: 100px;

            .header__greeting {
                max-width: 728px;
                margin-right: 0;

                h1.header__title {
                    min-height: 230px;
                    font-size: 68px;
                    line-height: 70px;
                }

                h1 img {
                    height: 49px;
                }

                .line {
                    margin: 15px 0 40px;
                }

                .btn-container {
                    .btn-classic {
                        padding: 12px 30px;
                        font-size: 18px;
                    }

                    .maska {
                        font-size: 18px;
                        padding: 12px 30px;
                    }
                }
            }

            .header__animation {
                margin-top: 70px;
                max-width: 55%;

                img {
                    object-fit: contain;
                    max-width: 100%;
                }
            }
        }
    }

    section.skills {
        padding: 130px 0 0;

        .container {
            max-width: 960px;
            align-items: start;
            flex-direction: column;

            .skills__container {
                max-width: 728px;
                width: 100%;
                margin: 0 auto;

                .section__description {
                    max-width: 650px;
                }

                .skills__box {
                    max-width: 728px;
                    margin-top: 40px;

                    .skills__item-container {
                        &:last-child {
                            margin-top: 30px;
                        }

                        .skills__items {
                            .item {
                                margin-right: 7%;
                            }
                        }
                    }
                }
            }

            .sphere__container {
                margin: 70px auto;
                max-width: 728px;
            }
        }
    }

    section.portfolio {
        .back__text {
            left: 20%;
            font-size: 220px;
        }

        .container {
            max-width: 960px;

            .portfolio__text-container {
                margin: 0 auto;
                max-width: 728px;

                .section__description {
                    margin-top: 12px;
                }
            }

            .gallery__container {
                margin: 70px auto 0;
                max-width: 728px;
                .gallery__item {
                    .gallery__preview {
                        &::after {
                            width: 50%;
                            height: 50%;
                            font-size: 19px;
                        }
                    }
                }

            }
        }
    }

    section.contacts {
        padding: 70px 0 70px;

        .back__text {
            top: 35px;
            left: 20%;
            font-size: 170px;
        }

        .container {
            max-width: 768px;
        }

        .form__container {
            .section__description {
                max-width: 470px;
            }

            flex-direction: column;

            .form__area {
                margin-right: 0;
                margin-bottom: 40px;
                max-width: 100%;

                form {
                    align-items: end;
                }
            }

            .maps {
                max-width: 100%;
            }
        }
    }
}

@media (max-width: 768px) {
    header {
        .header__firstscreen-container {
            margin-top: 130px;
            padding-bottom: 100px;

            .header__greeting {
                margin: 0 auto;
                display: inline-block;
                max-width: 500px;

                h1.header__title {
                    min-height: 210px;
                    font-size: 49px;
                    line-height: 62px;
                }

                h1 img {
                    height: 42px;
                }

                .line {
                    margin: 15px 0 40px;
                }

                .btn-container {
                    .btn-classic {
                        padding: 12px 30px;
                        font-size: 18px;
                    }

                    .maska {
                        font-size: 18px;
                        padding: 12px 30px;
                    }
                }
            }

            .header__animation {
                margin-top: 70px;
                max-width: 65%;

                img {
                    object-fit: contain;
                    max-width: 100%;
                }
            }
        }
    }

    section.skills {
        padding: 100px 0 10px;

        .container {
            .skills__container {
                max-width: 500px;
            }

        }
    }

    section.portfolio {
        padding: 90px 0 60px;

        .back__text {
            top: 55px;
            left: 25%;
            font-size: 125px;
        }

        .container {
            max-width: 960px;

            .portfolio__text-container {
                margin: 0 auto;
                max-width: 500px;
            }
            .gallery__container{
                display: flex;
                flex-direction: column;
                align-items: center;
                max-width: 500px;
                .gallery__item{
                    width: 80%;
                    position: relative;
                    margin-bottom: 20px;
                    .gallery__preview{
                        &::after{
                            width: 40%;
                            height: 40%;
                            font-size: 21px;
                        }
                    }
                }        
            }
        }
    }

    section.contacts {
        padding: 65px 0 70px;

        .back__text {
            top: 85px;
            left: 20%;
            font-size: 100px;
        }
    }
}

@media (max-width: 520px) {
    header {
        .header__nav-container {
            .header__nav-wrapper {
                padding: 10px;

                .header__nav {
                    width: 44px;
                    height: 34px;

                    .nav__icon {
                        width: 38px;
                        height: 4px;

                        &::before,
                        &::after {
                            width: 44px;
                            height: 4px;
                        }
                    }
                }

                .header__nav.active {
                    .nav__icon {
                        &::before {
                            width: 40px;
                        }

                        &::after {
                            width: 40px;
                        }
                    }
                }

                .btn-tool {
                    margin-left: 12px;
                    padding: 6px 8px;
                    font-size: 16px;
                    line-height: 16px;
                    border: 1.5px solid $mcolor;
                    border-radius: 6px;

                    svg {
                        width: 22px;
                        height: 18px;
                    }
                }
            }

            .header__nav-content {
                display: block;
                transition: opacity .3s ease-in .3s;

                .logo__container {
                    padding-top: 63px;
                    padding-bottom: 30px;

                    svg {
                        margin-top: 15px;
                        width: 75px;
                        height: 77px;
                    }

                    .name__title {
                        margin-top: 5px;
                        font-family: 'Montserrat-Medium', sans-serif;
                        font-size: 18px;
                        line-height: 20px;
                        letter-spacing: 0.1em;
                        text-transform: uppercase;
                        color: $whitetext;
                    }

                    .name__description {
                        margin-top: 5px;
                        font-family: 'Roboto-Regular', sans-serif;
                        font-size: 16px;
                        line-height: 18px;
                        letter-spacing: 0.04em;
                        color: rgb(190, 190, 190);
                    }
                }

                .nav__list-container {
                    margin-top: 50px;

                    li {
                        padding: 18px 20px;
                        font-size: 18px;
                        letter-spacing: 0.01em;
                        text-align: center;
                        border-top: 1px solid rgb(48, 48, 48);
                    }
                }

                .social__tl-container {
                    display: flex;
                    justify-content: center;
                    margin-top: 35px;
                    padding: 0 15px;
                }
            }
        }

        .header__firstscreen-container {
            margin-top: 90px;
            padding-bottom: 60px;

            .header__greeting {
                margin: 0 auto;
                display: block;
                max-width: 100%;

                h1.header__title {
                    min-height: 180px;
                    font-size: 44px;
                    line-height: 54px;
                    letter-spacing: 0.02em;

                    &::after {
                        display: none;
                    }
                }

                h1 span {
                    margin-right: 2px;
                }

                h1 img {
                    height: 31px;
                }

                .line {
                    margin: 10px 0 30px;
                }

                .btn-container {
                    width: 220.49px;
                    display: block;
                    margin: 0 auto;
                }
            }

            .header__animation {
                margin-top: 60px;
                max-width: 80%;
            }
        }

    }

    .back__text {
        display: none;
    }

    .container {
        padding: 0 15px;
    }

    .inputs__container {
        display: block !important;
    }

    .section__title {
        font-size: 40px;
        line-height: 52px;
    }

    section.skills {
        padding: 60px 0 50px;

        .container {
            .skills__box {
                .item {
                    margin-right: 25px !important;
                }
            }
            .sphere__container {
                display: block;
                width: 100%;
                margin: 50px 0 15px;
            }

            .tags__sphere {
                margin: 0 auto;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 374px;
                width: 374px;
            }

            .tagcloud {
                font-size: 22px;
                transform: scale(0.88);
            }
        }
    }

    section.portfolio {
        padding: 60px 0;
        .container {
            .gallery__container{
                .gallery__item{
                    width: 100%;
                    .gallery__preview{
                        &::after{
                            width: 40%;
                            height: 40%;
                            font-size: 21px;
                            transition: all .2s;
                        }
                    }
                }        
            }
        }
    }

    section.contacts {
        padding: 50px 0 40px;

        .form__container {
            .form__area {
                margin-bottom: 35px;

                .btn-container {
                    .btn-classic {
                        padding: 10px 20px;
                        border: 2px solid #64FFDA;
                        font-size: 17px;
                        transition: none;
                    }

                    .maska {
                        padding: 10px 20px;
                        font-size: 17px;
                        border: 1px solid $mcolor;
                        transition: none;
                    }
                }
            }
        }
    }

    footer {
        padding: 20px 10px;

        .footer__text {
            font-size: 14px;
            line-height: 19px;
        }
    }
}

@media (max-width: 410px) {
    header {
        .header__firstscreen-container {
            .header__greeting {
                h1.header__title {
                    min-height: 170px;
                    font-size: 39px;
                    line-height: 52px;
                }

                h1 img {
                    height: 26px;
                }

                .line {
                    margin: 10px 0 30px;
                }

                .btn-container {
                    width: 220.49px;
                    display: block;
                    margin: 0 auto;
                }
            }

        }

    }

    section.skills {
        padding: 30px 0 40px;

        .container {
            .skills__items {
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                .item {
                    margin-right: 25px !important;
                    margin-bottom: 15px;
                }
            }
            .tags__sphere {
                margin: 0 auto;
                height: 276px;
                width: 276px;
            }

            .tagcloud {
                margin: -30px;
                transform: scale(0.73);
            }
        }
    }
}