@keyframes btn-classic-on {
    from {
        mask-position: 0 0;
    }

    to {
        mask-position: 100% 0;
    }
}

@keyframes btn-classic-off {
    from {
        mask-position: 100% 0;
    }

    to {
        mask-position: 0 0;
    }
}

// Typing h1 element
@keyframes scaled {
    0% {
        transform: scale(1, 1);
        opacity: 1;
    }

    50% {
        transform: scale(1.25, 1.1);
        opacity: 0.7;
    }

    75% {
        transform: scale(0.85, 0.95);
        opacity: 0.9;
    }

    100% {
        opacity: 1;
        transform: scale(1, 1);
    }
}


@keyframes bounceIn {
    0% {
        opacity: 0;
        transform: scale3d(0.3, 0.3, 0.3);
    }

    20% {
        transform: scale3d(1.1, 1.1, 1.1)
    }

    40% {
        transform: scale3d(0.9, 0.9, 0.9)
    }

    60% {
        opacity: 1;
        transform: scale3d(1.03, 1.03, 1.03)
    }

    80% {
        transform: scale3d(0.97, 0.97, 0.97)
    }

    100% {
        opacity: 1;
        transform: scale3d(1, 1, 1)
    }
}
@keyframes rubberBand {
    0% {
        color: $mcolor;
        text-shadow: 7px 7px 15px rgba($color:  $mcolor, $alpha: .7),
                    -7px 7px 15px rgba($color:  $mcolor, $alpha: .7),
                    -7px -7px 15px rgba($color:  $mcolor, $alpha: .7),
                    7px -7px 15px rgba($color:  $mcolor, $alpha: .7);
        transform: scale3d(1, 1, 1)
    }

    30% {
        transform: scale3d(1.25, 0.75, 1)
    }

    40% {
        transform: scale3d(0.75, 1.25, 1)
    }

    50% {
        transform: scale3d(1.15, 0.85, 1)
    }

    65% {
        transform: scale3d(0.95, 1.05, 1)
    }

    75% {
        transform: scale3d(1.05, 0.95, 1)
    }

    100% {
        text-shadow: 7px 7px 15px rgba($color:  $mcolor, $alpha: .7),
                    -7px 7px 15px rgba($color:  $mcolor, $alpha: .7),
                    -7px -7px 15px rgba($color:  $mcolor, $alpha: .7),
                    7px -7px 15px rgba($color:  $mcolor, $alpha: .7);
        transform: scale3d(1, 1, 1)
    }
}
@keyframes rubberTitle{
    0%{
        color: rgba($color:  $mcolor, $alpha: 0);
        transform: rotate(-45deg) translateY(0%);
    }
    25%{
        color: rgba($color:  $mcolor, $alpha: 0.9);
        transform: rotate(-10deg) translateY(-25%);
    }
    50%{
        color: rgba($color:  $mcolor, $alpha: 0.5);
        transform: rotate(10deg) translateY(-25%);
    }
    75%{
        color: rgba($color:  $mcolor, $alpha: 0.7);
        transform: rotate(-10deg) translateY(-25%);
    }
    100%{
        color: rgba($color:  $mcolor, $alpha: 1);
        transform: rotate(0deg) translateY(0);
    }
} 

@keyframes rotating {
    0% {
        transform: rotate(-45deg);
    }
    70% {
        transform: rotate(10deg);
    }
    100% {
        transform: rotate(0deg);
    }
}
@keyframes moveGradient {
    50% {
      background-position: 100% 50%;
    }
}

@keyframes hidePreloader {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes spin {
    0%   {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 