*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    background: $bgcolor;
    overflow-x: hidden;
}

body {
    font-family: 'Roboto-Regular', sans-serif;
    font-size: 16px;
    color: $whitetext;
    overflow-x: hidden;
}

a {
    text-decoration: none;
    background: none;
    color: unset;
}

img {
    user-select: none;
}

#preloader {
    position: fixed;
    background-color: $bgcolor;
    z-index: 200;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#loader {
    display: block;
    position: relative;
    left: 50%;
    top: 50%;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #64FFDA;
    animation: spin 2s linear infinite;
}

#loader:before {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    right: 5px;
    bottom: 5px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #05ffee;
    animation: spin 3s linear infinite;
}

#loader:after {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #00ffc3;
    -webkit-animation: spin 1.5s linear infinite;
    animation: spin 1.5s linear infinite;
}

#preloader.hide-preloader {
    animation: hidePreloader 1s ease-out;
}

#preloader.preloader-hidden {
    display: none;
}


.label__group {
    width: 100%;
    position: relative;
    margin-bottom: 25px;

    .focus-animation {
        position: absolute;
        display: block;
        bottom: -2;
        left: 0;
        height: 2px;
        border-radius: 0 0 3px 3px;
        opacity: 0;
        width: 100%;

        &::after {
            content: "";
            display: block;
            height: 2px;
            width: 0;
            background: $mcolor;
            border-radius: 0 0 3px 3px;
            transition: all .3s ease-out;
        }
    }

    input,
    textarea {
        display: block;
        position: relative;
        padding: 15px 17px;
        width: 100%;
        font-family: 'Roboto-Regular', sans-serif;
        font-size: 15px;
        line-height: 17px;
        color: #DDDDDD;
        background: rgba(255, 255, 255, 0.1);
        border: none;
        border-radius: 3px;
        caret-color: $mcolor;

    }

    textarea {
        resize: vertical;
        min-height: 180px;
        margin-right: 0;
    }

    input:focus,
    input.valued,
    textarea:focus,
    textarea.valued {
        border-radius: 3px 3px 0 0;
        outline: none;

        &~.form__label {
            top: -18px;
            left: 0;
            font-size: 13px;
            line-height: 14px;
            color: $mcolor;
            opacity: 1;
            transition: all .3s;
        }

        &~.form__label.wrong {
            width: 100%;
            color: red;
            transition: none;
            top: 0;
            transform: translateY(calc(-100% - 5px));
            padding: 4px 3px;
            border-radius: 0 0 3px 3px;
            border-top: 3px solid red;
            background: #393939;
        }

        &~.focus-animation::after {
            width: 100%;
        }
    }



    .form__label {
        pointer-events: none;
        position: absolute;
        top: 16px;
        left: 17px;
        font-family: 'Roboto-Regular';
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 17px;
        color: #DDDDDD;
        transition: all .3s;

    }
}


.container {
    margin: 0 auto;
    max-width: 1020px;
    width: 100%;
    padding: 0 30px;
}

.btn-container {
    position: relative;
    display: inline-block;

    .btn-classic {
        position: relative;
        z-index: 1;
        padding: 15px 35px;
        border: 2px solid #64FFDA;
        border-radius: 0;
        font-family: 'Roboto-Regular', sans-serif;
        font-size: 20px;
        text-align: center;
        letter-spacing: 0.085em;
        text-transform: uppercase;
        color: $whitetext;
        background: $bgcolor;
        overflow: hidden;
        transition: 0.5s;
        mask: url("https://raw.githubusercontent.com/robin-dela/css-mask-animation/master/img/urban-sprite.png");
        mask-size: 3000% 100%;
        cursor: pointer;
        animation: btn-classic-off .8s steps(29) forwards;

        &:hover {
            animation: btn-classic-on 1s 0.2s steps(29) forwards;
        }
    }

    .btn-classic:disabled {
        cursor: auto;
        opacity: 0.4;
        animation: none;
        color: $whitetext;

        &:hover {
            animation: none;
        }

        &+.maska {
            color: $whitetext;
            opacity: 0;
        }
    }

    .maska {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        width: 100%;
        height: 100%;
        padding: 15px 35px;
        font-family: 'Roboto-Regular', sans-serif;
        font-size: 20px;
        letter-spacing: 0.085em;
        text-transform: uppercase;
        color: rgb(75, 73, 73);
        overflow: hidden;
        background: $mcolor;
        border: 2px solid $mcolor;
    }

    .btn-classic:focus+span {
        box-shadow: inset 0 0 0 2px $bgcolor;
    }
}

.btn-tool {
    min-height: 34px;
    margin-left: 15px;
    padding: 6px 10px;
    background: $bgcolor;
    font-family: 'Montserrat-Regular', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    letter-spacing: -0.005em;
    color: $whitetext;
    border: 1.5px solid $mcolor;
    border-radius: 8.46602px;
    transition: all .3s ease .2s;
    cursor: pointer;

    svg {
        display: inline-block;
        vertical-align: text-top;
        margin-right: 2px;
        width: 24px;
        height: 20px;
        fill: $mcolor;
        transition: all 0.5s ease .3s;
    }

    svg:has(path[stroke-width]) {
        fill: none;

        path {
            stroke: $mcolor;
            transition: all .3s ease .2s;
        }
    }

    &:hover {
        background: $mcolor;
        color: $bgcolor;

        svg {
            fill: $bgcolor;
        }

        svg:has(path[stroke-width]) path {
            stroke: $bgcolor;
        }
    }
}

.btn-tool.sound {
    .mute__on {
        display: none;
    }

    .mute__off {
        display: inline-block;
    }
}

.btn-tool.sound.active {
    background: $mcolor;
    color: $bgcolor;

    svg:has(path[stroke-width]) path {
        stroke: $bgcolor;
    }

    .mute__on {
        display: inline-block;
    }

    .mute__off {
        display: none;
    }
}

.line {
    display: block;
    width: 60%;
    height: 3px;
    border-radius: 0 2px 2px 0;
    background: $mcolor;
}

.section__title {
    display: inline-block;
    max-width: 450px;
    margin-bottom: 10px;
    font-family: 'Montserrat-Bold', sans-serif;
    font-size: 45px;
    line-height: 58px;
    letter-spacing: 0.04em;
    color: $mcolor;
    cursor: default;
    user-select: none;

    span {
        display: inline-block;
        transition: all .3s ease-in-out;
        opacity: 0;
    }

    span.animatedText {
        animation-fill-mode: both;
    }

    span.animatedText.bounceIn {
        animation-name: bounceIn;
        animation-duration: .75s
    }

    span.animatedText.rubberBand {
        animation-name: rubberTitle;
        animation-duration: .5s;
    }

    &::after {
        content: "";
        margin-top: 10px;
        display: block;
        width: 0%;
        height: 3px;
        border-radius: 0 2px 2px 0;
        background: $mcolor;
        transition: width .5s ease-out;
    }
}

.section__title.active {
    &::after {
        width: 80%;
    }
}

.section__description {
    display: block;
    margin-top: 25px;
    font-family: 'Roboto-Regular', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: rgba($color: $whitetext, $alpha: 0.9);
}

.back__text {
    position: absolute;
    top: 35px;
    right: -100px;
    z-index: -1;
    font-family: 'Montserrat-Black';
    font-size: 250px;
    letter-spacing: 0.12em;
    color: #2B2B2B;

}

.tagcloud {
    user-select: none;
    font-family: 'Montserra-Medium', sans-serif;
    font-size: 20px;
    margin: auto;
    width: 50%;
}

.tagcloud--item {
    color: $mcolor;
    transition: all .3s;
}

.tagcloud--item:hover {
    text-shadow: 0 0 5px #03e9f4,
        0 0 12px #03e9f4,
        0 0 25px #03e9f4,
        0 0 50px #03e9f4;
}