.animated{
    opacity: 0;
}
.animated.active{
    transition: opacity .3s ease;
    opacity: 1;
}
.animated.fade-in{
    transition: opacity .8s ease-in-out;
}

.animated.active.slide-down{
    animation: slideDown 1s ease-in-out;
}
@keyframes slideDown{
    from{
        transform: translateY(-100px);
    } to {
        transform: translateY(0);
    }
}
@keyframes slideDown2 {
    from{
        transform: translateY(-40px);
    } to {
        transform: translateY(0);
    }
}
.animated.active.slide-top{
    animation: slideTop .5s ease-in-out;
}
@keyframes slideTop {
    from{
        transform: translateY(100px);
    } to {
        transform: translateY(0);
    }
}


.animated.active.slide-right{
    animation: slideRight 1s ease-in-out;
}
@keyframes slideRight {
    from{
        transform: translateX(-100%);
    } to {
        transform: translateX(0);
    }
}

.animated.slide-left.active{
    animation: slideLeft 1.2s ease-out;
}
@keyframes slideLeft {
    from{
        transform: translateX(100%);
    } to {
        transform: translateX(0);
    }
}


.animated.animated.change-width60.active{
    animation: changeWidth 1s ease-in;
}
@keyframes changeWidth {
    0%{
        width: 0;
    }
    50%{
        width: 70%;
    }
    100%{
        width: 60%;
    }
}

.animated.zoom-out.active{
    animation: zoomOut 1s ease-in;
}
@keyframes zoomOut {
    from{
        transform: scale(0.8);
    } to {
        transform: scale(1);
    }
}

.animated.zoom-out-bezier.active{
    animation: zoomOutBezier 1s cubic-bezier(.2,.61,.31,.4);
}
@keyframes zoomOutBezier{
    0%{
        transform: scale(0.9);
    }
    50%{
        transform: scale(0.8)
    }
    100%{
        transform: scale(1);
    }
}

.animated.delay2x{
    animation-delay: .3s;
}