header{
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
    .header__nav-container{
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;
        height: 73px;
        width: 100%;

        .header__page-line{
            position: absolute;
            top: 0;
            left: 0;
            z-index: 3;
            width: 100%;
            height: 6px;
            background: #3A3A3A;
            .page-line{
                display: block;
                width: 0;
                height: 100%;
                background: $mcolor;
            }
        }
        .header__nav-wrapper{
            position: absolute;
            top: 6px;
            left: 0;
            z-index: 3;
            width: 100%;
            padding: 18px 30px 10px 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .header__nav {
                width: 48px;
                height: 39px;
                display: flex;
                align-items: center;
                justify-content: start;
                cursor: pointer;
                &:hover .nav__icon{
                    background: $whitetext;
                    &::before, &::after{
                        background: $whitetext;
                    }
                }
                .nav__icon{
                    position: relative;
                    width: 40px;
                    height: 5px;
                    border-radius: 3px;
                    background: rgba($color:  $whitetext, $alpha: 1);
                    transition: all .3s;
                    &::before, &::after{
                        content: "";
                        position: absolute;
                        width: 48px;
                        height: 5px;
                        border-radius: 3px;
                        background: inherit;
                        transition: inherit;
                    }
                    &::before{
                        border-radius: 3px;
                        top: -12px;
                    }
                    &::after{
                        border-radius: 3px;
                        bottom: -12px;
                    }
                }
            }
            .header__nav.active{
                .nav__icon{
                    background: $whitetext;
                    transform: rotate(-45deg);
                    &::before{
                        transform: rotate(90deg);
                        top: 0;
                    }
                    &::after{
                        bottom: 0;
                    }
                }
            }
            .header__tools{
                display: flex;
                align-items: center;
            }
        }
        .header__nav-content{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 0;
            opacity: 0;
            overflow: hidden;
            background: $bgcolor;
            transition: opacity .5s ease-in .3s;
            .logo__container{
                display: flex;
                flex-direction: column;
                align-items: center;
                background: #000;
                padding-top: 73px;
                padding-bottom: 30px;
                svg{
                    margin-top: 20px;
                    width: 70px;
                    height: 72px;
                }
                .name__title{
                    margin-top: 5px;
                    font-family: 'Montserrat-Medium', sans-serif;
                    font-size: 18px;
                    line-height: 20px;
                    letter-spacing: 0.1em;
                    text-transform: uppercase;
                    color: $whitetext;
                }
                .name__description{
                    margin-top: 5px;
                    font-family: 'Roboto-Regular', sans-serif;
                    font-size: 16px;
                    line-height: 18px;
                    letter-spacing: 0.04em;
                    color: rgb(190, 190, 190);
                }
            }
            .nav__list-container {
                li{
                    width: 100%;       
                    padding: 15px 20px;
                    font-family: 'Roboto-Regular', sans-serif;
                    font-size: 16px;
                    letter-spacing: 0.01em;
                    text-align: center;
                    border-top: 1px solid rgb(48, 48, 48);
                    a{
                        color: $whitetext;
                        transition: all .5s;
                    }
                }
                li:last-child{
                    border-bottom: 1px solid rgb(48, 48, 48)
                }
                li.active a, li a:hover{
                    color: $mcolor;
                }
            }
            .social__tl-container{
                display: flex;
                align-items: center;
                margin: 0 auto 10px;
                padding: 0 15px;
                font-family: 'Roboro-Regular', sans-serif;
                font-size: 16px;
                svg{
                    margin-right: 5px;
                }
                a{
                    margin-left: 5px;
                    font-family: 'Montserrat-Medium', sans-serif;
                    color: $mcolor;
                }
            }
        }
    }
    .header__nav-container.active{
        height: 100vh;
        .header__nav-content{
            height: 100vh;
            overflow: visible;
            opacity: 1;
        }
    }
    .header__firstscreen-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 130px;
        padding-bottom: 120px;
        .header__greeting{
            max-width: 490px;
            width: 100%;
            h1.header__title{
                margin: 0;
                display: block;
                min-height: 221px;
                font-family: 'Montserrat-bold', sans-serif;
                font-weight: 900;
                font-size: 65px;
                line-height: 77px;
                color: $whitetext;  
            }
            h1 *{
                opacity: 0;
                transition: all .3s ease-out;
            }
            h1 img{
                display: inline-block;
                vertical-align: baseline;
                object-fit: contain;
                height: 50px;
                margin-right: 3px;
            }
            h1 span{
                display: inline-block;
                transition: all .3s ease-in-out;
                &:hover{
                    color: $mcolor;
                    text-shadow: 7px 7px 15px rgba($color:  $mcolor, $alpha: .7),
                    -7px 7px 15px rgba($color:  $mcolor, $alpha: .7),
                    -7px -7px 15px rgba($color:  $mcolor, $alpha: .7),
                    7px -7px 15px rgba($color:  $mcolor, $alpha: .7);
                }
                
            }
            }
            h1 *.animatedText {
                animation-fill-mode: both;
            }
            h1 span.animatedText.bounceIn{
                animation-name: bounceIn;
                animation-duration: .75s
            }
            h1 span.animatedText.rubberBand{
                animation-name: rubberBand;
                animation-duration: .75s;
            }
            h1 img.animatedText.rotated{
                animation-duration: .75s;
                animation-name: rotating;
                opacity: 1 !important;
            }
            .line{
                margin: 30px 0 60px;
            }
        }
        .header__animation{
            max-width: 400px;
            img{
                object-fit: contain;
                max-width: 400px;
            }
        }
    }

section.skills{
    padding: 130px 0;
    background: rgba(217, 217, 217, 0.12);
    .container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .skills__container{
            max-width: 450px;
            .skills__box{
                margin-top: 55px;
                .skills__item-container{
                    &:last-child{
                        margin-top: 40px;
                    }
                    h5.skills__item-title{
                        display: block;
                        margin-bottom: 10px;
                        font-family: 'Montserrat-Medium', sans-serif;
                        font-size: 22px;
                        line-height: 150%;
                        text-transform: uppercase;
                        color: $whitetext;
                    }
                    .skills__items{
                        display: flex;
                        .item{
                            position: relative;
                            margin-right: 25px;
                            width: 50px;
                            height: 50px;
                            position: relative;
                            border-radius: 8px;
                            &::after{
                                    position: absolute;
                                    content: "";
                                    top: 0;
                                    left: 0;
                                    z-index: -1;
                                    width: 100%;
                                    height: 100%;
                                    background: linear-gradient(
                                      60deg,
                                      hsl(224, 85%, 66%),
                                      hsl(269, 85%, 66%),
                                      hsl(314, 85%, 66%),
                                      hsl(359, 85%, 66%),
                                        $mcolor,
                                      hsl(134, 85%, 66%),
                                      hsl(179, 85%, 66%)
                                    );
                                    background-size: 300% 300%;
                                    background-position: 0 50%;
                                    border-radius: 8px;
                                    animation: moveGradient 2s alternate infinite;
                            }
                            svg{
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%,-50%);
                                width: 39px;
                                height: 39px;
                            }
                        }
                    }
                }
            }
        }

    }
}

section.portfolio{
    position: relative;
    padding: 125px 0 120px;
    .gallery__container{
        margin-top: 100px;
        width: 100%;
        height: auto;
        display: flex;
        .gallery__item{
            width: calc(100% / 3);
            position: relative;
            &::after{
                content: '';
                padding-top: 100%;
            }
            .gallery__preview{
                position: relative;
                width: 100%;
                height: 100%;
                overflow: hidden;
                cursor: pointer;
                &::after{
                    content: 'ВЫБРАТЬ';
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%) scale(0.5);
                    width: 40%;
                    height: 40%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 50%;
                    font-family: 'Montserrat-Light', sans-serif;
                    font-size: 21px;
                    background-color: $mcolor;
                    color: #000;
                    opacity: 0;
                    transition: all .3s ease-in .3s;
                    user-select: none;
                }
                img{
                    display: block;
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                    opacity: 0.4;
                    transition: all .3s ease-in .5s;
                }
            }
            .gallery__preview:hover{
               &::after{
                    opacity: 1;
                    transform: translate(-50%, -50%) scale(1);
                } 
                img{
                    transform: scale(1.1);
                    opacity: 1;
                }
            }
            .gallery__modal-window{
                display: none;
            }
        }        
    }
}

section.contacts{
    position: relative;
    padding: 125px 0 120px;
    .back__text{
        top: 60px;
        right: -250px;
        font-size: 210px;
    }
    .form__container{
        display: flex;
        justify-content: space-between;
        align-items: start;
        .form__area{
            display: block;
            width: 100%;
            margin-right: 25px;
            max-width: 490px;
            form{
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-top: 40px;
                .inputs__container{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    .label__group:first-child{
                        margin-right: 20px;
                    }
                }
            }
            .btn-container{
                .btn-classic {
                    padding: 13px 25px;
                    font-size: 18px;
                }
                .maska {
                    padding: 13px 25px;
                    font-size: 18px;
                }
            }
        }
    }
    .maps{
        margin-top: 25px;
        width: 100%;
        max-width: 440px;
        img{
            width: 100%;
        }
    }
}

footer{
    padding: 25px 10px;
    text-align: center;
    background: #000;
    .footer__text{
        font-family: 'Montserrat-Light', sans-serif;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 0.12em;
        color: rgba(255, 255, 255, 0.86);
    }
}

